.feedback{
    border: 2px solid #445f87;
    border-radius: 10px;
    margin: 40px 0;
    &__inner{
        display: flex;
        align-items: center;
        padding: 23px 15px;
        .section-title{
            margin-bottom: 0;
            margin-left: auto;
            flex-shrink: 1;
        }
        .input{
            width: 340px;
            &-wrap{
                &:not(:last-of-type){
                    margin-right: 20px;
                }
            }
        }
        .Btn{
            margin-left: 50px;
        }
    }
    &-brims{
        display: flex;
        align-items: center;
        margin-left: 50px;
        margin-right: auto;
    }
}
@media(max-width: 1300px){
    .feedback__inner {
        .input{
            width: 280px;
        }
    }
}
@media(max-width: 1199px){
    .feedback__inner {
        flex-direction: column;
        justify-content: center;
        padding: 26px 15px 30px;
        .section-title{
            margin-left: 0;
            margin-bottom: 30px;
            width: 100%;
        }
        .feedback-brims{
            margin-right: 0;
            width: 100%;
            margin-left: 0;
        }
        .input-wrap{
            flex-shrink: 1;
            &:not(:last-of-type){
                margin-right: 10px;
            }
            width: calc(50% - 73px);
        }
        .input{
            width: 100%;
        }
        .Btn{
            margin-left: 10px;
        }
    }
}
@media(max-width: 767px){
    .feedback{
        margin: 30px 0;
    }
}
@media(max-width: 600px){
    .feedback__inner{
        padding-top: 15px;
        padding-bottom: 20px;
        .section-title{
            margin-bottom: 18px;
        }
        .feedback-brims{
            flex-direction: column;
        }
        .input-wrap{
            width: 100%;
            &:not(:last-of-type){
                margin-bottom: 10px;
                margin-right: 0;
            }
        }
        .Btn{
            margin-left: 0;
            margin-top: 20px;
        }
    }
}